/* notice */
.notice .notice-line {
  position: relative;
}
.notice .notice-line .bg-left {
  position: absolute; top: 0; left: 0; width: 50%; height: 100%; background-color: #333;
}
.notice .notice-line .bg-right {
  position: absolute; top: 0; right: 0; width: 50%; height: 100%; background-color: #f6f5ef;
}
.notice .notice-line .inner {
  height: 62px; display: flex; overflow: hidden;
}
.notice .notice-line .inner .inner__left {
  width: 60%; height: 100%; background-color: #333; display: flex; align-items: center; 
}
.notice .notice-line .inner .inner__right {
  width: 40%; height: 100%; display: flex; justify-content: flex-end; align-items: center;
}
.notice .notice-line .inner .inner__left h2 {
  color: #fff; font-size: 17px; font-weight: 700; margin-right: 70px;
}
.notice .notice-line .inner .inner__left .swiper {
  height: 62px; flex-grow: 1;
}
.notice .notice-line .inner .inner__left .notice-line__more {
  width: 62px; height: 62px; display: flex; justify-content: center; align-items: center;
}
.notice .notice-line .inner .inner__left .notice-line__more .material-icons {
  color: #fff; font-size: 30px;
}
.notice .notice-line .inner .inner__right h2 {
  font-size: 17px; font-weight: 700; margin-right: 20px;
}
.notice .notice-line .inner .inner__right .toggle-promotion {
  width: 62px; height: 62px; cursor: pointer; display: flex; justify-content: center; align-items: center; 
}
.notice .notice-line .inner .inner__right .toggle-promotion .material-icons {
  font-size: 30px;
}
.notice .notice-line .inner .inner__left .swiper .swiper-slide {
  height: 62px; display: flex; align-items: center; color: #fff;
}

.notice .promotion {
  height: 693px; background-color: #f6f5ef; position: relative; transition: height .4s; overflow: hidden;
}
.notice .promotion.hide {
  height: 0;
}
.notice .promotion .swiper {
  width: calc(819px * 3 + 20px); height: 553px; position: absolute; top: 40px; left: 50%; margin-left: calc((819px * 3 + 20px) / -2);
}
.notice .promotion .swiper .swiper-slide {
  opacity: .5; transition: opacity 1s;
}
.notice .promotion .swiper .swiper-slide-active {
  opacity: 1;
}
.notice .promotion .swiper .swiper-slide .btn {
  position: absolute; bottom: 0; left: 0; right: 0; margin: auto;
}
.notice .promotion .swiper-pagination{
  bottom: 40px;
  font: 0;
  right: 0;
  margin: auto;
}

.notice .promotion .swiper-pagination .swiper-pagination-bullet{
  background-image: url(../images/promotion_slide_pager.png);
  width: 12px;
  height: 12px;
  margin-right: 6px;
  outline: 0;
  background-color: transparent;
}
.notice .promotion .swiper-pagination .swiper-pagination-bullet:last-child{
  margin-right: 0;
}
.notice .promotion .swiper-pagination .swiper-pagination-bullet-active{
  background-image: url(../images/promotion_slide_pager_on.png);
}
.notice .promotion .swiper-button-prev,
.notice .promotion .swiper-button-next{
  width: 42px;
  height: 42px;
  border: 2px solid #333;
  border-radius: 50%;
  position: absolute;
  top: 300px;
  z-index: 1;
  cursor: pointer;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255,255,255,0.3);
  transition: .4;
  user-select: none;
}
.notice .promotion .swiper-button-prev{
  left: 50%;
  margin-left: -480px;
  color: #669900;
}
.notice .promotion .swiper-button-next{
  right: 50%;
  margin-right: -480px;
  color: #669900;
}
.notice .promotion .swiper-button-prev::after,
.notice .promotion .swiper-button-next::after{
  content: '';
}

.notice .promotion .swiper-button-prev:hover,
.notice .promotion .swiper-button-next:hover{
  background-color: #333;
  color: #fff;
}

